<template>
  <div class="container-fluid">
	  <div class="card mb-12">
		<div class="row no-gutters">
			<div class="col-md-4">
			<img src="static/img/familia.png" class="img-fluid rounded img-thumbnail" alt="Candidato" />
			</div>
			<div class="col-md-8">
			<div class="card-body">
				<h4 class="card-title font-weight-bold">Podemos La Rinconada - Presentación</h4>
				<p style="line-height: 1.38; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Este programa pol&iacute;tico ha sido el resultado del trabajo municipalista que durante este tiempo ha venido realizando Podemos La Rinconada. Un proyecto &nbsp;que concurre a las pr&oacute;ximas Municipales en un contexto de resaca electoral por las &uacute;ltimas Generales. Conviene por tanto, tomar un poco de perspectiva para contextualizar nuestras propuestas.</span></p><p>&nbsp;</p><p style="line-height: 1.2; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Podemos La Rinconada tiene su origen y bebe de un descontento institucional que fue se&ntilde;alado por las movilizaciones del 15M. Movimiento surgido durante los d&iacute;as previos a la jornada de reflexi&oacute;n de las elecciones auton&oacute;micas y municipales de 2011. Aquel movimiento expres&oacute; el hartazgo de una amplia base social que no se sent&iacute;a representada y por ello denunciaba la baja calidad democr&aacute;tica de Espa&ntilde;a. Este descontento fue puesto de relieve con unos m&eacute;todos, como la &ldquo;no violencia&rdquo;, que fueron imitados y ampliaron la base social del mismo con una movilizaci&oacute;n internacional el 15 de Octubre del mismo a&ntilde;o.</span></p><p style="line-height: 1.2; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">En este tiempo acelerado parece que queda bastante lejos aquel momento fundante. Y esa distancia parece desdibujar los contornos de nuestro proyecto. Por eso conviene echar la vista atr&aacute;s, para tomar perspectiva y poder afrontar los retos del presente.</span></p><p>&nbsp;</p><p style="line-height: 1.2; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Nada ha sido f&aacute;cil durante este tiempo. Vivimos en un entorno globalizado en el que los extremismos y la pol&iacute;tica-ficci&oacute;n, ha ido recobrando protagonismo. La historia va por ciclos y en este tiempo, parec&iacute;a que el mundo cada vez se hac&iacute;a m&aacute;s intolerante y empezaba a recordarnos &eacute;pocas pasadas. Pero Espa&ntilde;a, ante lo que parec&iacute;a un retroceso en la conquista de nuestros Derechos, ha sabido reaccionar y ha frenado a los partidos que quer&iacute;an rebobinar la historia. Ahora lo que nos toca, es transformar el alivio en alegr&iacute;a.</span></p><p>&nbsp;</p><p style="line-height: 1.2; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Hemos comprobado, como aquellos cuyo proyecto se basa en discusiones bizantinas, los debates personalistas y la batalla dogm&aacute;tica han sido los claros perdedores. Por eso presentamos este programa pol&iacute;tico con m&aacute;s de 100 iniciativas. Porque La Rinconada merece que sus pol&iacute;ticos tiren de inventiva m&aacute;s que de entra&ntilde;as para resolver nuestros problemas.</span></p><p>&nbsp;</p><p style="line-height: 1.2; margin-top: 0pt; margin-bottom: 0pt; text-align: justify;"><span style="  color: #000000; background-color: transparent; font-weight: 400; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Y una vez contextualizado nuestro proyecto, ahora nos toca hablar de todas las ideas que transformar&aacute;n a La Rinconada. Queremos una Rinconada que se sepa fuerte en la diversidad, feminista, donde por fin baje el paro del 22% y que sea considerada como una joya de la naturaleza sevillana. Pero esto solo puede ser posible si contamos contigo.</span></p>
				<p class="card-text"><small class="text-muted">La Rinconada entre tod@s, para tí también.</small></p>
			</div>
			</div>
		</div>
		<div class="row">
			<div class="col-12">
				<router-link to="/" class="btn btn-podemos" style="margin-left: 1.25rem">Consulte nuestras propuestas</router-link>
			</div>
		</div>
	</div>
  </div>
</template>

